import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES } from '@shared';
import { Category } from '@models';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<Category>;
  displayedColumns: string[] = ['categoryDescription', 'status'];
  statusList = [...STATUSES];

  category = new Category();
  categoryList = [];
  isExist = false;
  categoryForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
    // this.getProductList();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.category).subscribe((res: any) => {
      this.categoryList = res.responseData.data;
      this.dataSource = new MatTableDataSource<Category>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.category, this.category).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
      }
    )
  }

  // not in used
  checkCategoryIfExisting(categoryName): void {
    let category = [];
    this.apiService.findAll(ENDPOINTS.category).subscribe(
      (res: any) => {
        category = res.responseData.data;
        category = category.filter(cn => cn.description === categoryName);
        if (category.length > 0) {
          this.sweetAlertService.customErrorMessage('Category name already exist.');
        } else {
          if (this.category.id > 0) {
            this.update();
          } else {
            this.save();
          }
        }
      }
    )
  }

  getCategory(): void {
    this.apiService.findById(ENDPOINTS.category, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.category = res.responseData.data;
      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.category, this.category).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    this.category.status = COMMON_FUNCTION.ACTIVE;
    this.categoryForm = this.formBuilder.group({
      description: ['', Validators.required],
      status: [''],
    });
  }

  resetFormValidator(): void {
    this.category = new Category();
    this.category.status = COMMON_FUNCTION.ACTIVE;
    this.categoryForm.markAsUntouched();
    this.categoryForm.markAsPristine();
  }

  delete() {

    Swal.fire({
      text: 'Are you sure you want to delete this company?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.category, this.category.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Category successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }, (err: any) => {
            this.sweetAlertService.error(err);
          }, () => {
            this.resetFormValidator();
            this.getList();
          });
      }
    });
  }

  // getProductList() {
  //   this.apiService.findAll(ENDPOINTS.product).subscribe((res: any) => {
  //     this.productList = res.responseData.data.filter((product) => product.status === COMMON_FUNCTION.ACTIVE);
  //   });
  // }

  // compareProduct(value: any, selected: any): boolean {
  //   if (value && selected) {
  //     return value.description === selected.description;
  //   }
  // }

}
